.container.footer_container {
  flex-direction: row;
}

.footer {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #0E0E0E;
  overflow: hidden;
}

.footer_left {
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: 37%;
  border-right: 1px solid #202020;
  color: #fff;
}

.footer_left_title {
  font-size: 34px;
  font-weight: 500;
  line-height: 44px;
  margin: 120px 0 30px;
}

.footer_left_text {
  font-size: 16px;
  font-weight: 400;
  max-width: 240px;
}

.footer_left_list {
  padding: 75px 0 80px;
}

.footer_left_list_item {
  color: #969696;
  margin: 0 0 10px;
}

.footer_left_list_link {
  text-decoration: none;
  color: #fff;
}

.footer .footer_right {
  display: flex;
  justify-content: left;
  align-items: flex-start;
  flex-direction: column;
  margin: 125px 0 0 100px;
  width: 63%;
  position: relative;
}

.footer .footer_right_title {
  font-family: Inter;
  font-size: 15px;
  font-weight: 500;
  line-height: 18px;
  letter-spacing: 0em;
  text-align: left;
  color: #969696;
}

.footer .footer_right_list_links {
  display: flex;
  width: 100%;
  max-height: 100px;
  margin: 130px 0 175px;
  z-index: 2;
}

.footer .footer_right_list_link {
  text-decoration: none;
  color: #fff;
  position: relative;
}

.footer .footer_right_list_link::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 2px;
  transform: scale(0);
  background-color: #fff;
  bottom: 0;
  left: 0;
  transform-origin: bottom right;
  transition: transform 0.25s ease-out;
}

.footer .footer_right_list_link:hover::after {
  transform: scaleX(1);
  transform-origin: bottom left;
}

.footer .footer_right_list {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  width: inherit;
  gap: 10px;
}

.footer .footer_right_circle {
  position: absolute;
  width: 641px;
  height: 641px;
  border-radius: 50%;
  background-color: #202020;
  z-index: 1;
  right: -30%;
  bottom: -45%;
}